'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { format } from 'date-fns';
import { 
  FiChevronRight, 
  FiPlus, 
  FiMoreVertical, 
  FiSearch, 
  FiMessageCircle, 
  FiBell,
  FiHeart,
  FiActivity,
  FiFileText,
  FiClock,
  FiPackage,
  FiUser,
  FiCalendar,
  FiMessageSquare,
  FiPhone,
  FiSettings,
  FiLogOut
} from 'react-icons/fi';
import PatientSidebar from '@/components/PatientSidebar';

export default function PatientProfilePage() {
  const { user, loading: authLoading } = useAuth();
  const router = useRouter();
  const [patientData, setPatientData] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);

  useEffect(() => {
    if (!authLoading && (!user || user.role !== 'patient')) {
      router.push('/login');
      return;
    }
    if (user && user.role === 'patient') {
      fetchPatientData();
    }
  }, [user, authLoading, router]);

  const fetchPatientData = async () => {
    try {
      setLoading(true);
      // Use logged-in user's ID (patient viewing their own profile)
      const patientId = user?.id;

      if (!patientId) {
        console.error('User not logged in');
        setLoading(false);
        return;
      }

      // Fetch patient user data using the me/profile endpoint
      const userResponse = await api.get('/users/me/profile');
      const patientUser = userResponse.data.user;
      
      if (!patientUser) {
        console.error('User profile not found');
        setLoading(false);
        return;
      }

      // Calculate age
      let age = { years: 0, months: 0 };
      if (patientUser.dateOfBirth) {
        const birthDate = new Date(patientUser.dateOfBirth);
        const today = new Date();
        let years = today.getFullYear() - birthDate.getFullYear();
        let months = today.getMonth() - birthDate.getMonth();

        if (months < 0) {
          years--;
          months += 12;
        }
        if (today.getDate() < birthDate.getDate()) {
          months--;
          if (months < 0) {
            years--;
            months += 11;
          }
        }
        age = { years, months };
      }

      // Fetch prescriptions for medications (filter by patient email)
      const prescriptionsResponse = await api.get(`/prescriptions?limit=100`);
      let allPrescriptions = prescriptionsResponse.data.prescriptions || [];
      // Filter prescriptions for current patient
      allPrescriptions = allPrescriptions.filter((prescription: any) => 
        prescription.patient?.email === patientUser.email || prescription.patientId === patientId
      );
      const prescriptions = allPrescriptions;

      // Extract medications from prescriptions
      const medications = prescriptions
        .flatMap((prescription: any) => prescription.medicines || [])
        .filter((med: any, index: number, self: any[]) => {
          const name = typeof med === 'string' ? med : med.name || '';
          return self.findIndex((m: any) => (typeof m === 'string' ? m : m.name || '') === name) === index;
        })
        .map((med: any) => {
          if (typeof med === 'string') {
            return { name: med, brand: '' };
          }
          return {
            name: med.name || med,
            brand: med.dosage || med.frequency || '',
          };
        });

      // Fetch appointments for notes and history
      const appointmentsResponse = await api.get(`/appointments?patientId=${patientId}&limit=100`);
      const appointments = appointmentsResponse.data.appointments || [];

      // Create notes from prescriptions
      const notes = prescriptions
        .filter((prescription: any) => prescription.instructions || prescription.diagnosis)
        .map((prescription: any) => ({
          date: prescription.prescriptionDate || prescription.createdAt,
          content: prescription.instructions || prescription.diagnosis || '',
        }))
        .sort((a: any, b: any) => new Date(b.date).getTime() - new Date(a.date).getTime());

      // Format patient data
      const profileImageUrl = patientUser?.profileImage 
        ? `data:${patientUser.profileImageMimeType || 'image/jpeg'};base64,${patientUser.profileImage}`
        : null;

      setPatientData({
        id: patientUser.id?.slice(0, 7) || '',
        name: patientUser.name || '',
        profileImage: profileImageUrl,
        dateOfBirth: patientUser.dateOfBirth || null,
        weight: patientUser.weight || '',
        height: patientUser.height || '',
        age,
        address: patientUser.address || '',
        mobilePhone: patientUser.phone || '',
        homePhone: patientUser.homePhone || '',
        workPhone: patientUser.workPhone || '',
        email: patientUser.email || '',
        allergies: Array.isArray(patientUser.allergies) ? patientUser.allergies : [],
        medications,
        vitals: {
          bloodPressure: patientUser.bloodPressure || '',
          pulse: patientUser.pulse || '',
        },
        notes,
        labResults: prescriptions
          .filter((prescription: any) => prescription.tests && prescription.tests.length > 0)
          .flatMap((prescription: any) =>
            (prescription.tests || []).map((test: any) => ({
              name: typeof test === 'string' ? test : test.name || test,
              date: prescription.prescriptionDate || prescription.createdAt,
            }))
          )
          .sort((a: any, b: any) => new Date(b.date).getTime() - new Date(a.date).getTime()),
      });
    } catch (error) {
      console.error('Error fetching patient data:', error);
    } finally {
      setLoading(false);
    }
  };

  const getInitials = (name: string) => {
    return name
      ?.split(' ')
      ?.map((n: string) => n[0])
      ?.join('')
      ?.toUpperCase()
      ?.slice(0, 2) || 'U';
  };

  const formatDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return format(date, 'MM/dd/yyyy');
    } catch {
      return dateString;
    }
  };

  if (authLoading || loading) {
    return (
      <div className="flex min-h-screen bg-gray-50">
        <PatientSidebar user={user} logout={() => {}} />
        <main className="ml-64 flex-1">
          <Loading />
        </main>
      </div>
    );
  }

  if (!user || user.role !== 'patient') {
    return null;
  }

  if (!patientData) {
    return (
      <div className="flex min-h-screen bg-gray-50">
        <PatientSidebar user={user} logout={() => {}} />
        <main className="ml-64 flex-1 p-8">
          <div className="bg-white rounded-lg shadow-md p-12 text-center">
            <p className="text-gray-500 text-lg">No profile data available. Please refresh the page.</p>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="flex min-h-screen bg-gray-50">
      {/* Collapsible Left Sidebar */}
      <div className={`${sidebarCollapsed ? 'w-16' : 'w-64'} bg-teal-600 text-white transition-all duration-300 fixed left-0 top-0 h-full z-20`}>
        <div className="p-4">
          {/* Collapse/Expand Button */}
          <button
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
            className="w-full flex items-center justify-center p-2 hover:bg-teal-700 rounded-lg transition-colors mb-4"
          >
            <FiChevronRight className={`text-xl transform transition-transform ${sidebarCollapsed ? '' : 'rotate-180'}`} />
          </button>

          {/* Medical Symbol */}
          {!sidebarCollapsed && (
            <div className="flex items-center justify-center mb-6">
              <div className="w-12 h-12 bg-white bg-opacity-20 rounded-full flex items-center justify-center">
                <span className="text-2xl">ðŸ©º</span>
              </div>
            </div>
          )}

          {/* Patient Profile Section */}
          {!sidebarCollapsed && (
            <div className="mb-6 pb-6 border-b border-teal-500">
              <div className="flex items-center gap-3 mb-3">
                <div className="w-10 h-10 rounded-full bg-white bg-opacity-20 flex items-center justify-center text-sm font-semibold">
                  {getInitials(patientData.name)}
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-semibold truncate">{patientData.name}</p>
                  <p className="text-xs text-teal-200">ID: {patientData.id}</p>
                </div>
              </div>
            </div>
          )}

          {/* Navigation Links */}
          <nav className="space-y-1">
            {[
              { href: '#overview', label: 'OVERVIEW', icon: FiUser, active: true },
              { href: '#history', label: 'HISTORY', icon: FiClock },
              { href: '#medications', label: 'MEDICATIONS', icon: FiPackage },
              { href: '#allergies', label: 'ALLERGIES', icon: FiUser },
              { href: '#results', label: 'RESULTS', icon: FiCalendar },
              { href: '#vaccines', label: 'VACCINES', icon: FiMessageSquare },
              { href: '#messages', label: 'MESSAGES', icon: FiMessageCircle },
              { href: '#calls', label: 'CALLS', icon: FiPhone },
              { href: '#files', label: 'FILES', icon: FiFileText },
              { href: '#billing', label: 'BILLING', icon: FiFileText },
            ].map((item) => {
              const Icon = item.icon;
              return (
                <a
                  key={item.href}
                  href={item.href}
                  className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
                    item.active
                      ? 'bg-teal-700 text-white font-semibold'
                      : 'text-teal-100 hover:bg-teal-700 hover:text-white'
                  }`}
                >
                  <Icon className="text-xl flex-shrink-0" />
                  {!sidebarCollapsed && <span className="text-sm">{item.label}</span>}
                </a>
              );
            })}
          </nav>

          {/* Logout Button */}
          {!sidebarCollapsed && (
            <div className="absolute bottom-4 left-4 right-4">
              <button className="w-full flex items-center gap-3 px-4 py-3 rounded-lg text-teal-100 hover:bg-teal-700 transition-colors">
                <FiLogOut className="text-xl" />
                <span className="text-sm">LOGOUT</span>
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Main Content */}
      <main className={`${sidebarCollapsed ? 'ml-16' : 'ml-64'} flex-1 transition-all duration-300`}>
        {/* Header */}
        <header className="bg-white border-b border-gray-200 px-8 py-4 flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-800 uppercase tracking-wide">PATIENT OVERVIEW</h1>
          <div className="flex items-center gap-4">
            <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
              <FiSearch className="text-xl text-gray-600" />
            </button>
            <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
              <FiMessageCircle className="text-xl text-gray-600" />
              <span className="absolute top-0 right-0 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center">
                2
              </span>
            </button>
            <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
              <FiBell className="text-xl text-gray-600" />
              <span className="absolute top-0 right-0 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center">
                2
              </span>
            </button>
            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white font-bold">
              {getInitials(user?.name || 'D')}
            </div>
          </div>
        </header>

        {/* Content Grid */}
        <div className="p-8">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Patient Information Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <div className="flex flex-col items-center text-center mb-6">
                <div className="w-24 h-24 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white text-3xl font-bold mb-4">
                  {patientData.profileImage ? (
                    <img 
                      src={patientData.profileImage} 
                      alt={patientData.name}
                      className="w-full h-full rounded-full object-cover"
                    />
                  ) : (
                    getInitials(patientData.name)
                  )}
                </div>
                <h2 className="text-2xl font-bold text-gray-800 mb-2">{patientData.name}</h2>
                <div className="inline-block px-3 py-1 bg-red-100 text-red-700 rounded-full text-sm font-medium mb-4">
                  Allergy
                </div>
                <div className="grid grid-cols-2 gap-4 w-full text-left">
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">DOB</p>
                    <p className="text-sm font-semibold text-gray-800">{formatDate(patientData.dateOfBirth)}</p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">Weight</p>
                    <p className="text-sm font-semibold text-gray-800">{patientData.weight} lb</p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">Age</p>
                    <p className="text-sm font-semibold text-gray-800">
                      {patientData.age?.years || 0}y {patientData.age?.months || 0}m
                    </p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">Height</p>
                    <p className="text-sm font-semibold text-gray-800">{patientData.height}</p>
                  </div>
                </div>
                <button 
                  onClick={() => router.push(`/doctor/chat?userId=${patientData.id}`)}
                  className="w-full mt-6 px-4 py-2 bg-teal-600 text-white rounded-lg hover:bg-teal-700 transition-colors font-medium"
                >
                  SEND MESSAGE
                </button>
              </div>
            </div>

            {/* Current Medications Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-800">Current medications</h3>
                <div className="flex items-center gap-2">
                  <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                    <FiPlus className="text-gray-600" />
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                    <FiMoreVertical className="text-gray-600" />
                  </button>
                </div>
              </div>
              <div className="space-y-3">
                {patientData.medications?.map((med: any, idx: number) => (
                  <div key={idx} className="flex items-start gap-3 p-3 bg-gray-50 rounded-lg">
                    <div className="w-8 h-8 rounded-full bg-teal-100 flex items-center justify-center flex-shrink-0 mt-0.5">
                      <FiPackage className="text-teal-600" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-semibold text-gray-800">{med.name}</p>
                      <p className="text-xs text-gray-600">{med.brand}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Contact Information Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">Contact Information</h3>
              <div className="space-y-3 text-sm">
                <div>
                  <p className="text-xs text-gray-500 uppercase mb-1">Home Address</p>
                  <p className="text-gray-800">{patientData.address}</p>
                </div>
                <div>
                  <p className="text-xs text-gray-500 uppercase mb-1">Mobile Phone #</p>
                  <p className="text-gray-800">{patientData.mobilePhone}</p>
                </div>
                <div>
                  <p className="text-xs text-gray-500 uppercase mb-1">Home Phone #</p>
                  <p className="text-gray-800">{patientData.homePhone}</p>
                </div>
                <div>
                  <p className="text-xs text-gray-500 uppercase mb-1">Work Phone #</p>
                  <p className="text-gray-800">{patientData.workPhone}</p>
                </div>
                <div>
                  <p className="text-xs text-gray-500 uppercase mb-1">Email</p>
                  <p className="text-gray-800">{patientData.email}</p>
                </div>
              </div>
            </div>

            {/* Vitals Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-800">Vitals</h3>
                <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                  <FiMoreVertical className="text-gray-600" />
                </button>
              </div>
              <div className="space-y-4">
                <div className="flex items-center gap-4 p-4 bg-gray-50 rounded-lg">
                  <div className="w-12 h-12 rounded-full bg-red-100 flex items-center justify-center">
                    <FiHeart className="text-red-600 text-xl" />
                  </div>
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">Blood Pressure</p>
                    <p className="text-lg font-bold text-gray-800">{patientData.vitals?.bloodPressure || 'N/A'}</p>
                  </div>
                </div>
                <div className="flex items-center gap-4 p-4 bg-gray-50 rounded-lg">
                  <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center">
                    <FiActivity className="text-blue-600 text-xl" />
                  </div>
                  <div>
                    <p className="text-xs text-gray-500 uppercase mb-1">Pulse</p>
                    <p className="text-lg font-bold text-gray-800">{patientData.vitals?.pulse || 'N/A'}</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Notes Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-800">Notes</h3>
                <div className="flex items-center gap-2">
                  <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                    <FiPlus className="text-gray-600" />
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                    <FiMoreVertical className="text-gray-600" />
                  </button>
                </div>
              </div>
              <div className="space-y-3">
                {patientData.notes?.map((note: any, idx: number) => (
                  <div key={idx} className="p-4 bg-gray-50 rounded-lg">
                    <p className="text-xs text-gray-500 mb-2">{formatDate(note.date)}</p>
                    <p className="text-sm text-gray-800 leading-relaxed">{note.content}</p>
                  </div>
                ))}
              </div>
            </div>

            {/* Lab Results Card */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-800">Lab results</h3>
                <button className="p-1 hover:bg-gray-100 rounded transition-colors">
                  <FiMoreVertical className="text-gray-600" />
                </button>
              </div>
              <div className="space-y-3">
                {patientData.labResults?.map((result: any, idx: number) => (
                  <div key={idx} className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                    <div className="w-8 h-8 rounded-full bg-teal-100 flex items-center justify-center flex-shrink-0">
                      <FiFileText className="text-teal-600" />
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-semibold text-gray-800 truncate">{result.name}</p>
                      <p className="text-xs text-gray-600">{formatDate(result.date)}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

